import { TemplateRef } from '@angular/core';

/**
 * The point passed to the tooltip template.
 */
export class TooltipTemplatePoint {
  /**
   * The point value.
   */
    public value: any;

  /**
   * The point category. Available only for Categorical series.
   */
    public category: string|Date|number;

  /**
   * The point series options.
   */
    public series: any;

  /**
   * The point `dataItem`.
   */
    public dataItem: any;

  /**
   * The point value represented as a percentage value. Available only for the Donut, Pie, and 100% Stacked charts.
   */
    public percentage: number;

  /**
   * The sum of point values since the last `"runningTotal"` summary point. Available for the Waterfall series.
   */
    public runningTotal: number;

  /**
   * The sum of all previous series values. Available for the Waterfall series.
   */
    public total: number;

  /**
   * The point error bar low value. Available for the Categorical series with error bars.
   */
    public low: number;

  /**
   * The point error bar high value. Available for the Categorical series with error bars.
   */
    public high: number;

  /**
   * The point error bar x low value. Available for the Scatter series with error bars.
   */
    public xLow: number;

  /**
   * The point error bar x high value. Available for the Scatter series with error bars.
   */
    public xHigh: number;

  /**
   * The point error bar y low value. Available for the Scatter series with error bars.
   */
    public yLow: number;

  /**
   * The point error bar y low value. Available for the Scatter series with error bars.
   */
    public yHigh: number;

    private point: any;
    private format: string;
    private template: TemplateRef<any>;

    constructor(point: any, format?: string, template?: TemplateRef<any>) {
        this.value = point.value;
        this.category = point.category;
        this.series = point.series;
        this.dataItem = point.dataItem;
        this.percentage = point.percentage;
        this.runningTotal = point.runningTotal;
        this.total = point.total;
        this.low = point.low;
        this.high = point.high;
        this.xLow = point.xLow;
        this.xHigh = point.xHigh;
        this.yLow = point.yLow;
        this.yHigh = point.yHigh;

        this.template = template;
        this.point = point;
        this.format = format;
    }

    private get formattedValue(): string {
        return this.format ? this.point.formatValue(this.format) : String(this.value);
    }
}



